import * as THREE from 'three';

export default class Sizes
{
  static SIZES;

  constructor( props )
  {
    //  singleton
    if( Sizes.SIZES != undefined )
    {
      return Sizes.SIZES;
    }
    Sizes.SIZES = this;

    this.state = {};
    this.props = props;

    this.init();
  }

  init()
  {
    this.width = window.innerWidth;
    this.height = window.innerHeight;
    this.pixelRatio = window.devicePixelRatio;
  }

  resize()
  {
    this.width = window.innerWidth;
    this.height = window.innerHeight;
    this.pixelRatio = window.devicePixelRatio;
  }

}