import * as THREE from 'three';

import Scene from './Scene';
import Camera from './Camera';
import Renderer from './Renderer';
import Sizes from './utils/Sizes';
import Time from './utils/Time';
import Debug from './utils/Debug';

export default class TheWorld
{
  static instance;

  constructor( props = {} )
  {
    //  pseudo singleton
    if( TheWorld.instance != undefined )
    {
      return TheWorld.instance;
    }
    TheWorld.instance = this;

    //  check canvas
    if( props.canvas == undefined || !props.canvas.getContext )
    {
      const _message = 'The argument does not contain a CANVAS element.';
      throw new Error( _message );
      return;
    }

    this.state = {}
    this.props = props;

    // this.canvas;
    // this.time;
    // this.eventList;
    // this.scene;
    // this.camera;
    // this.renderer;

    //  pseudo init
    ( async ()=>{

      await this.init();
      await this.sleep();
      await this.addEvents();
      await this.sleep();

      this.startRender();

    })();

  }

  //  initialzie 
  init()
  {
    return new Promise( resolve =>{
      this.eventList = []
      this.canvas = this.props.canvas;


      let _bgColor = 0xF0F0F0;

      this.sizes = new Sizes();
      this.time = new Time();
      this.scene = new Scene();
      this.camera = new Camera();
      this.renderer = new Renderer();
      this.debug = new Debug();

      resolve();

    })
  }

  update()
  {
    this.camera.update();
    this.renderer.render( this.scene.instance, this.camera.instance );
  }

  startRender()
  {
    this.stopRender();
    this.time.start();
  }

  stopRender()
  {
    this.time.stop();
  }

  addEvent( _evt )
  {
    //  check argument
    if( _evt.target == undefined || _evt.event == undefined || _evt.function == undefined )
    {
      const _message = 'The argument does not contain a target or event or function.';
      throw new Error( _message );
      return;
    }

    //  check option
    if( _evt.option == undefined )
    {
      _evt.option = {};
    }

    _evt.target.addEventListener( _evt.event, _evt.function, _evt.option );
    this.eventList.push( _evt );

  }

  addEvents()
  {
    //  resize window
    var _evt0 = {
      target: window,
      event: 'resize',
      function: ( event ) => 
      {
        this.sizes.resize();
        this.renderer.resize();
        this.camera.resize();
      },
      option: {}
    }
    this.addEvent( _evt0 );

    //  tick
    var _evt1 = {
      target: document,
      event: 'tick',
      function: ( event ) => 
      {
        this.update();
      },
      option: {}
    }
    this.addEvent( _evt1 );

  }

  removeEvent( _evt )
  {
    let _len = this.eventList.length;
    while( _len )
    {
      _len --;
      let _evt0 = this.eventList[ _len ];
      if( _evt0.target == _evt.target && _evt0.event == _evt.event && _evt0.function == _evt.function )
      {
        _evt.target.removeEventListener( _evt.event, _evt.function );
        this.eventList.splice( _len, 1 );
        break;
      }
    }
  }

  removeEvents()
  {
    let len = this.eventList.length;
    while( len )
    {
      len--;
      let _evt = this.eventList.pop();
      _evt.target.removeEventListener( _evt.event, _evt.function );
      _evt = null;
    }

    this.eventList = [];
  }

  add( _mesh )
  {
    this.scene.add( _mesh );
  }

  remove( _mesh )
  {
    this.scene.remove( _mesh );
  }

  addPass( _pass )
  {
    this.renderer.addPass( _pass );
  }

  removePass( _pass )
  {
    this.renderer.removePass( _pass );
  }

  sleep( _duration )
  {
    _duration = _duration == undefined? 16 : _duration;
    return new Promise( resolve => {
      setTimeout( resolve, _duration );
    })
  }

  dispose()
  {
    //  
    //  Traverse the whole scene
    this.scene.traverse((child) =>
    {
        // Test if it's a mesh
        if( child instanceof THREE.Mesh )
        {
            child.geometry.dispose();

            // Loop through the material properties
            for( const key in child.material )
            {
                const value = child.material[key];

                // Test if there is a dispose function
                if( value && typeof value.dispose === 'function' )
                {
                    value.dispose()
                }
            }
        }
    })

    // this.camera.controls.dispose();
    this.renderer.instance.dispose();
  }
}