import * as THREE from 'three';
import { FBXLoader } from '/node_modules/three/examples/jsm/loaders/FBXLoader.js';
import { GLTFLoader } from '/node_modules/three/examples/jsm/loaders/GLTFLoader.js';

export default class resourceLoader
{
	static resource;
	static proress;

	static async load( _list )
	{

		resourceLoader.resource = _list;

		let _count = 0;
		let _len =  Object.keys( resourceLoader.resource ).length;
		for( var i in resourceLoader.resource )
		{
			await resourceLoader._loadResource( resourceLoader.resource[i] );

			if( resourceLoader.proress ) resourceLoader.proress( ++_count / _len );

			await resourceLoader.delay(16);
		}

	}

	static delay( _delay )
	{
		_delay = _delay == undefined ? 16 : _delay;
		return new Promise( resolve =>{
			setTimeout( ()=>{
				resolve();
			}, _delay );
		} )
	}

	static _loadResource( _obj )
	{
		return new Promise( resolve => {

			//	select loader by type
			const _type =  _obj.type.toLowerCase();

			const loader = 	_type == 'img'? new THREE.TextureLoader()	:
							_type == 'fbx'? new FBXLoader()				:	new GLTFLoader();

			loader.load(
				_obj.url,
				_resource =>{

					_obj.value = _resource;

					resolve();
				},
		    	_xhr => {

		    			// console.log((_xhr.loaded / _xhr.total) * 100 + '% loaded');

		    	},
				_error =>{

					resolve();

				});
		})
	}

	constructor()
	{
		return new Error('This class is static.');
	}
	
}