import * as THREE from 'three';

import fireWorkParams from './fireWorkParams';

import Botan from './Botan';
import Kiku from './Kiku';
import Kiku2 from './Kiku2';
import TheWorld from '../../TheWorld/js/TheWorld';

export default class fireWorkGenerator
{

    constructor( _fireWorkParams, _scene )
    {
        /**
         * 
         * objectList
         * 
         */

        this.pamrams = _fireWorkParams;
        this.scene = _scene;
        this.instanceList = [];

        this.isAlive = true;
        var N = this.pamrams.value.length;
        for( var i = 0; i < N; i++ )
        {
            var _param = this.pamrams.value[i];
            var _particles = this.createMesh( _param );
            this.scene.add( _particles.mesh );

            _particles.mesh.position.copy( this.pamrams.value[i].endPosition );
            this.instanceList.push( _particles );
        }
      
    }

    createMesh( _param )
    {
        // let _size = _param.size == 1 ? 30 : 160;

        var _btn;
        if( _param.type.toLowerCase() == 'botan' )
        {
            _btn = new Botan( _param );
        } else if( _param.type.toLowerCase() == 'kiku' )
        {
            _btn = new Kiku( _param );
        }


        // _btn = new Kiku2( _param );


        return _btn;
    }

    update()
    {
        let _delta = TheWorld.instance.time.delta;
        
        var len = this.instanceList.length;
        while( len )
        {
            len--
            let _instance = this.instanceList[len];
            _instance.update( _delta );
        }

        let _l = this.instanceList[0];
        if( _l.mesh.material.uniforms.time.value > _l.param.deadTime )
        {
            this.isAlive = false;
        }
    }

    dispose()
    {
        //  kill mesh

        let _len0 = this.instanceList.length;
        while( _len0 )
        {
            _len0--
            let _instance = this.instanceList.pop();
            this.scene.remove( _instance.mesh );
            _instance.dispose();
        }
        this.instanceList = null;

        this.scene = null;

        this.pamrams.dispose();
        this.pamrams = null;
    }

}