import TheWorld from './js/TheWorld.js';

import * as THREE from 'three';

import Contentbox from '../Contentbox.js';
import curve from '../curve.js'

import { OrbitControls } from '/node_modules/three/examples/jsm/controls/OrbitControls.js';
import { Reflector } from 'three/addons/objects/Reflector.js';

import './style.css';

import resourceLoader from '../resourceLoader.js';

import fireWorksScene from '../Artwork/js/fireWorksScene.js'


export default class StarPlatinum extends TheWorld
{
	constructor( props )
	{
		super( props );

		//	test code
		this.componentDidMountPseudoInit();
	}

	async componentDidMountPseudoInit()
	{
		//	preload resource / test code.
		let _sampleList = {

			'skyImage':
				{
					type: 'img',
					url: 'assets/img/evening_road_01_puresky.jpg',
					value: undefined
				}

				
		};

		//	resourceLoader.proress = ( _value )=>{	console.log( 'progress -', _value )};

		await resourceLoader.load(_sampleList).then(()=>{

			console.log('resourceLoader.load() done.', resourceLoader.resource);

		});


		//	wewbgl 
		{
			this.aw = new TheWorld();
			this.aw.time.start();

			this.aw.camera.focalLengthToFOV( 24 );
			// let _dist = this.aw.camera.pixelEqualMagnification();
			// this.aw.camera.instance.position.z = _dist;

			this.aw.camera.instance.position.z = 10;
			

			this.controls = new OrbitControls( this.aw.camera.instance, this.aw.props.canvas );
			this.controls.autoRotate = true;
			this.controls.autoRotateSpeed = 0.2;

			this.controls.enableDamping = true;
			this.controls.dampingFactor = 0.15;
			this.controls.enableZoom = true;

			this.controls.enabled = true;
			this.controls.target = this.aw.camera.focus;

			this.aw.camera.instance.position.y = 1.6;
			this.aw.camera.instance.far = 3000;
			this.aw.camera.instance.updateProjectionMatrix();
		}

		//	grid
		{
			const _wf00 = new THREE.GridHelper( 20, 20 )
			_wf00.material.transparent = true;
			_wf00.material.opacity = 0.25;
			this.aw.add( _wf00 );

			const _wf01 = new THREE.GridHelper( 200, 20 )
			_wf01.material.transparent = true;
			_wf01.material.opacity = 0.25;
			this.aw.add( _wf01 );

			const _wf012 = new THREE.GridHelper( 1000, 10 )
			_wf012.material.transparent = true;
			_wf012.material.opacity = 0.125;
			this.aw.add( _wf012 );

			const _axis = new THREE.AxesHelper( 500 );
			this.aw.add( _axis );
		}

		//	line
		if( false )
		{
			var __curve = [	new THREE.Vector3() ];

			for( var i = 0; i < 5; i++ )
			{
				var _vec3 = new THREE.Vector3( ( Math.random() - 0.5 ) * 30, Math.random() * 5, - ( i + 1 ) * 10 );
				__curve.push( _vec3 );

				//	pointer
				var _geom = new THREE.SphereGeometry( 0.2 )
				var _mate = new THREE.MeshBasicMaterial({color: 0xFF0000})
				var _sphere = new THREE.Mesh( _geom, _mate )
				this.aw.add( _sphere )
				_sphere.position.set( _vec3.x, _vec3.y, _vec3.z )
			}


			const _curve = curve.SplineCurve3D( __curve, 60 );

			let _len = _curve.length;
			let points = new Float32Array( _len * 3 );
			for( var i = 0; i < _len; i++ )
			{
				points[ i * 3 + 0 ] = _curve[ i ].x;
				points[ i * 3 + 1 ] = _curve[ i ].y;
				points[ i * 3 + 2 ] = _curve[ i ].z;
			}

			const geometry = new THREE.BufferGeometry()
			geometry.setAttribute( 'position', new THREE.BufferAttribute( points, 3 ) );
			const material = new THREE.LineBasicMaterial( { color: 0xff0000 } );

				// Create the final object to add to the scene
			const curveObject = new THREE.Line( geometry, material );
			this.aw.add( curveObject );
		}

		//	gltf
		if( false )
		{
			resourceLoader.resource.gltf01.value.scene.scale.set(0.3,0.3,0.3)
			this.aw.add( resourceLoader.resource.gltf01.value.scene )

			resourceLoader.resource.gltf01.value.scene.children[0].material.color = new THREE.Color().setHSL( Math.random(), 0.5, 0.8 );
			resourceLoader.resource.gltf01.value.scene.children[1].material.color = new THREE.Color().setHSL( Math.random(), 0.5, 0.8 );
		}


		//	lightworld
		this.aw.add( new THREE.AmbientLight( 0xFFFFFF, 1.0 ))
		this.dr = new THREE.DirectionalLight( 0xFFFFFF,1.2 )
		this.dr.position.set( 10, 16, 5 );
		this.aw.add( this.dr )

		//	skybox
		var _geometry = new THREE.SphereGeometry(1500)
		var _material = new THREE.MeshBasicMaterial({
			map: resourceLoader.resource.skyImage.value,
			side: THREE.BackSide,
			color: 0x24264C
		})
		
		this.sky = new THREE.Mesh( _geometry, _material );
		this.aw.add( this.sky );

		this.aw.scene.instance.environment = resourceLoader.resource.skyImage.value;
		this.aw.scene.instance.background = resourceLoader.resource.skyImage.value;
		this.aw.scene.instance.backgroundIntensity = 0.2
		// this.aw.scene.instance.backgroundBlurriness = 0.5

		this.aw.camera.instance.position.z = 150;
		this.aw.camera.focus.y = 100;



		//	shaderChank
		//	shaderLib
		// console.log( THREE )
		// for( var i in THREE )
		// {
		// 	if( typeof THREE[i] != 'function' )
		// 	{
		// 		console.log( i, THREE[i] )
		// 	}
		// }


		this.fws = new fireWorksScene();;

		//	reflector
		{
			let groundMirror, verticalMirror;
			let geometry, material;

			// geometry = new THREE.CircleGeometry( 500, 64 );
			geometry = new THREE.PlaneGeometry( 3000,3000 );
			groundMirror = new Reflector( geometry, {
				clipBias: .003,
				textureWidth: window.innerWidth * window.devicePixelRatio,
				textureHeight: window.innerHeight * window.devicePixelRatio,
				color: 0xb5b5b5
			} );
			groundMirror.position.y = - 0.5;
			groundMirror.rotateX( - Math.PI / 2 );
			this.aw.add( groundMirror );

			// geometry = new THREE.PlaneGeometry( 100, 100 );
			// verticalMirror = new Reflector( geometry, {
			// 	clipBias: 0.003,
			// 	textureWidth: window.innerWidth * window.devicePixelRatio,
			// 	textureHeight: window.innerHeight * window.devicePixelRatio,
			// 	color: 0xc1cbcb
			// } );
			// verticalMirror.position.y = 50;
			// verticalMirror.position.z = - 50;
			// this.aw.add( verticalMirror );

			//resize
			/*
			groundMirror.getRenderTarget().setSize(
					window.innerWidth * window.devicePixelRatio,
					window.innerHeight * window.devicePixelRatio
				);
			*/
		}



		
	}

	update()
	{
		super.update();

		if(  this.controls )
		{
			this.controls.update()
		}
	}

}
