import * as THREE from 'three';

export default class fireWorkParams
{
    constructor( props )
    {
        /**
         * 
         * 25
         * 10
         * 
         */
        var _N = Math.floor( Math.random() * Math.random() *  Math.random() * 4 ) + 2;
        var N = props.layerNums ? props.layerNums : _N;
        N = N < 2? 2 : N > 5 ? 5 : N;

        //  
        var _params = [];

        //  
        var _gravity = props.gravity ? props.gravity : 9.8;
        
        /*
         7
        0, 120, 240, 60, 300, (0,0,0.9), (0.6, 0.2, 1.0 )
        */
        var colorMax = Math.random() * 360;
        var colorMin = Math.random() * colorMax;
        var colorList =
        [
            { h: 0, s: 0.85, l: 0.5  },
            { h: 120, s: 0.85, l: 0.5  },
            { h: 240, s: 0.85, l: 0.5  },
            { h: 60, s: 0.85, l: 0.5  },
            { h: 300, s: 0.85, l: 0.5  },
            { h: 0, s: 0.0, l: 0.9  },
            { h: 60, s: 0.2, l:  1.0  }
        ];

        /**
         * 
         * i == 0  
         */
        for( var i = 0; i < N; i++ )
        {
            //  
            var _startColor = colorList[ ~~( Math.random() * colorList.length ) ];
            var _middleColor = colorList[ ~~( Math.random() * colorList.length ) ];
            var _endColor = colorList[ ~~( Math.random() * colorList.length ) ];

            var _param = {
                type: Math.random() < 0.5? "BOTAN" : "KIKU",    //  
                shape: Math.floor( Math.random() * 5 ),         //  

                lifeTime: 3.0,                                // 
                releaseDuration: 0.2,                        //   

                partCount: ( N * 100 ) - i * 100,  //  

                partSpeedVariance: 10,                        //  
                partSize: ( 10 + ~~( Math.random() * 6 ) ),           //  
                partSizeVariance: 1,                        //  ON,OFF(1,0)

                //  partWind: 50,                                 //  
                //  partFriction: 5,                              //  
                //  partGravity: 0.5,                             //  

                flickerDensity: .5,                           //  

                partLength: 64,                   //  , 

                partColors: [
                    new THREE.Color().setHSL( Math.random(), 0.65, 0.8 ),  //    
                    new THREE.Color().setHSL( Math.random(), 0.65, 0.7 ),  //  
                    new THREE.Color().setHSL( Math.random(), 0.65, 0.6 )   //  
                ],

                fworkSpeed: 1.0,                              //   
                fworkGravity: - _gravity,                              //  
                fworkHueMin: colorMin,                        //                          
                fworkHueMax: colorMax,                        //  
                fworkDuration: 1.2,                           //  

                size: ( 1.0 - i * ( 1.0 / N + ( Math.random()-0.5 ) * 0.2 ) ) * ( 1.0 + Math.random() * Math.random() ), 
                // size: ( 1.0 + Math.floor( Math.random() * 10 ) * 0.1 ) * ( ( i + 1 ) / N + ( Math.random()-0.5 ) * 0.1 ),  //  N

                deadTime: 1.0,     //     
                textures: [],     //   ,

                startPosition: new THREE.Vector3(),         //  
                endPosition: new THREE.Vector3(),         //  

                gravity: _gravity   //  
            };


            //  
            for( var j in props )
            {
                if( _param[j] )
                {
                    _param[j] = props[j];
                    if( j == 'size' )
                    {
                        _param.size = ( 1.0 - i * ( 1.0 / N + ( Math.random() - 0.5 ) * 0.2 ) ) * ( 1.0 + Math.random() * Math.random() ) * props[j];
                        // _param.partCount = ( ( N * 100 ) - i * 100 ) * ( _param.size * _param.size / 900 )
                    }
                    
                }
            }

            //  
            _param.deadTime = _param.lifeTime + _param.flickerDensity + _param.releaseDuration;

            //  
            _params[i] = _param;

        }

        this.value = _params;
      
    }

    dispose()
    {
        let len = this.value.length;
        while( len )
        {
            len--;
            let _p = this.value.pop();
            _p.textures = null;
            _p.position = null;
            _p.startPosition = null;
            _p.endPosition = null;
            _p.partColors = null;
            _p.mesh = null;

            _p = null;
        }

        this.value = null;
        
    }

}