import * as THREE from 'three';

export default class Kiku
{
	constructor( _param )
	{
		this.param = _param;
		this.mesh = undefined;
		this.init();
	}

    init()
    {
        var _param = this.param;
        var len = _param.partCount;

        var _geometry = new THREE.BufferGeometry();
        var _positions = [];
        var _colors = [];
        var _flickerDensity = [];
        var _drawNumbers = [];

        var _pos = new THREE.Vector3();
        var _color = new THREE.Color().setHSL( 0.0, 0.5, 0.55 );

        var _count = 0;
        for( var i = 0; i < len; i++ )
        {
            var _r = _param.size;

            var phi = Math.acos( - 1 + ( 2 * i ) / len);
            var theta = Math.sqrt( len * Math.PI ) * phi;

            phi += ( Math.random() - 0.5 ) * 0.1;
            theta += ( Math.random() - 0.5 ) * 0.1;

            _pos.setFromSphericalCoords( _r, phi, theta );

            var _len = _param.partLength;
            for( var j = 0; j < _len; j++ )
            {
                _positions[ ( _count * _len + j ) * 3 + 0 ] = _pos.x;
                _positions[ ( _count * _len + j ) * 3 + 1 ] = _pos.y;
                _positions[ ( _count * _len + j ) * 3 + 2 ] = _pos.z;

                _colors[ ( _count * _len + j ) * 3 + 0 ] = _color.r;
                _colors[ ( _count * _len + j ) * 3 + 1 ] = _color.g;
                _colors[ ( _count * _len + j ) * 3 + 2 ] = _color.b;

                _flickerDensity[ _count * _len + j ] = Math.random() * _param.flickerDensity;

                _drawNumbers[ _count * _len + j ] = Math.round( j / 2 );
            }

            _count++;

        }

        _geometry.setAttribute( 'position', new THREE.Float32BufferAttribute( _positions, 3 ) );
        _geometry.setAttribute( 'colors', new THREE.Float32BufferAttribute( _colors, 3 ) );
        _geometry.setAttribute( 'flickerDensity', new THREE.Float32BufferAttribute( _flickerDensity, 1 ).setUsage( THREE.DynamicDrawUsage ) );
        _geometry.setAttribute( 'drawNumber', new THREE.Float32BufferAttribute( _drawNumbers, 1 ).setUsage( THREE.DynamicDrawUsage ) );

        _geometry.rotateX( Math.random() * Math.PI * 2.0 );
        _geometry.rotateY( Math.random() * Math.PI * 2.0 );
        _geometry.rotateZ( Math.random() * Math.PI * 2.0 );
        _geometry.computeVertexNormals();
        _geometry.computeBoundingBox();
        _geometry.computeBoundingSphere();

        var _timeList = [];
        for( var i = 0; i < _param.partLength; i++ )
        {
            _timeList[i] = 0.0;
        }

        var _material = new THREE.ShaderMaterial({
            defines: {
                partLength: parseFloat( _param.partLength )
            },
            uniforms: {
                time: { value: 0.0  },
                times: { value: _timeList },
                gravity: { value: _param.gravity },
                // texture0: {  value: _param.textures[2] },
                // texture1: {  value: _param.textures[3] },
                color01: { value: _param.partColors[0] },
                color02: { value: _param.partColors[1] },
                color03: { value: _param.partColors[2] },
                lifeTime: {  value: _param.lifeTime },
                masterFlickerDensity: { value: _param.flickerDensity },
                releaseDuration: { value: _param.releaseDuration },
                totalSize: {  value: _param.size }
            },
            vertexShader: [
                "uniform float time;",
                "uniform float times["+_param.partLength+"];",
                "uniform float lifeTime;",
                "uniform float masterFlickerDensity;",
                "uniform float totalSize;",
                "uniform float gravity;",

                "attribute vec3 colors;",
                "attribute float flickerDensity;",
                "attribute float drawNumber;",

                "varying float vFlickerDensity;",
                "varying float vDrawNumber;",


                "float easeOutQuad(float t,float b,float c,float d)",
                "{",
                "   return -c *(t/=d)*(t-2.0) + b;",
                "}",

                "float easeOutCubic(float t,float b,float c,float d)",
                "{",
                "   return c*((t=t/d-1.0)*t*t + 1.0) + b;",
                "}",

                "float easeOutQuint(float t,float b,float c,float d)",
                "{",
                "   return c*((t=t/d-1.0)*t*t*t*t + 1.0) + b;",
                "}",

                "float easeOutExpo(float t,float b,float c,float d)",
                "{",
                "   return (t==d) ? b+c : c * 1.001 * (-pow(2.0, -10.0 * t/d) + 1.0) + b;",
                "}",

                "float easeOutSine( float t, float b, float c, float d )",
                "{",
                "   return c * sin(t/d * ( 3.141592 / 2.0 ) ) + b;",
                "}",


                "void main() {",
                "   vec3 pos = normalize( position );",

                "   int index = int(drawNumber);",
                "   float _timeValue = times[index];",

                //  check
                "   float _time = _timeValue > lifeTime + masterFlickerDensity? lifeTime + masterFlickerDensity : _timeValue;",

                "   float fireScale = easeOutSine( _time, 0.0, 1.0, lifeTime + masterFlickerDensity ) * totalSize;",
                "   vec3 _newPos = pos * fireScale;",
                "   _newPos.y -= _timeValue * _timeValue * 0.5 * gravity * 0.2;",    //  gravity
                "   pos = _newPos;",

                "   vFlickerDensity = flickerDensity;",
                "   vDrawNumber = drawNumber;",

                //   position
                "   gl_Position = projectionMatrix * modelViewMatrix * vec4( pos, 1.0 );",
                "}"
            ].join( "\n" ),
            fragmentShader: [
                "uniform float time;",
                "uniform float lifeTime;",
                "uniform float releaseDuration;",

                "uniform vec3 color01;",
                "uniform vec3 color02;",
                "uniform vec3 color03;",

                // "varying vec3 vColor;",
                "varying float vFlickerDensity;",
                "varying float vDrawNumber;",


                "float easeOutQuad(float t,float b,float c,float d)",
                "{",
                "   return -c *(t/=d)*(t-2.0) + b;",
                "}",

                "void main( void ) {",

                "   float _offset = 1.0 - ( time - ( lifeTime + vFlickerDensity ) ) / releaseDuration;",
                "   float _opacity = time / ( lifeTime + vFlickerDensity ) < 1.0? 1.0: _offset;",

                "   _opacity = _opacity * ( 1.0 - vDrawNumber / float(partLength) );",
                // "    _opacity = _opacity * smoothstep( 0.8 - 0.8 * ( lifeTime + vFlickerDensity - time ) / releaseDuration, 1.0, 1.0 - vDrawNumber / float(partLength) );",

                "    float _per = time / ( lifeTime + vFlickerDensity );",
                "    _opacity = _opacity * smoothstep( 0.65 - 0.65 * _per, 1.0 - 0.65 * _per * _per * _per, 1.0 - vDrawNumber / float(partLength) );",



                "   float _check = time / ( lifeTime + vFlickerDensity );",
                "   vec3 myColor = color01;",
                "   myColor = mix(color01, color02, smoothstep(0.0, 0.5, _check));",
                "   myColor = mix(color02, color03, smoothstep(0.5, 1.0, _check));",

                "   gl_FragColor = vec4( myColor, max( _opacity, 0.0 ) );",
                "}"
            ].join( "\n" ),
            blending: THREE.AdditiveBlending,
            depthTest: false,
            transparent: true,
            vertexColors: true
        });

        this.mesh = new THREE.LineSegments( _geometry, _material );

    }

	update( _delta = 0 )
	{
		this.mesh.material.uniforms.time.value += _delta;

		var _times = this.mesh.material.uniforms.times.value;
		var _len = _times.length;
		while( _len )
		{
			_len --;
			if( _len == 0 )
			{
				_times[0] += _delta;
                _times[0] = _times[0] < 0.0 ? 0.0 : _times[0];
			} else {
				_times[ _len ] = _times[_len-1];
			}
		}
	}

	dispose()
	{
        this.mesh.geometry.dispose();
        // this.mesh.material.map.dispose();
        // this.mesh.material.uniforms.texture0.value.dispose();
        // this.mesh.material.uniforms.texture1.value.dispose();
        this.mesh.material.dispose();
        this.mesh = null;

		this.param = null;
	}
}