/*
    fireWorksScene.js
*/

import * as THREE from "three";
import TheWorld from '../../TheWorld/js/TheWorld.js';
import fireWorkParams from "./fireWorkParams.js"; 
import fireWorkGenerator from "./fireWorkGenerator.js";

import Tweener from "../../Tweener.js";
import {Pane} from 'tweakpane';

export default class fireWorksScene{
  
  constructor(props){
    this.props = props;
    this.init();
    this.update()
  }

  init()
  {
    /**
     * words
     * https://www.ntt-f.co.jp/column/0050.html
     * http://www.japan-fireworks.com/yogo/jiten.html
     */

    //  get singleton threejs world.
    this.aw = new TheWorld();
    this.scene = new THREE.Scene();
    this.aw.add( this.scene )

    //  fireworks textures setting
    this.textures = [];
    this.textures[0] = new THREE.TextureLoader().load('assets/img/spark1.png');
    this.textures[1] = new THREE.TextureLoader().load('assets/img/circle1.png');
    this.textures[2] = new THREE.TextureLoader().load('assets/img/particle01.png');
    this.textures[3] = new THREE.TextureLoader().load('assets/img/particle02.png');
    this.textures[4] = new THREE.TextureLoader().load('assets/img/particle03.png');

    /**
     * 
     *      (cm)    
     * 2    6cm      60m     25m
     * 3    9cm      120m     30m
     * 4    12cm      150m     65m
     * 5    15cm      200m     85m
     * 6    18cm      220m     110m
     * 7    21cm      250m     120m
     * 8    24cm      280m     140m
     * 10   30cm      330m     160m   ()
     * 15   45cm      400m     200m
     * 20   60cm      500m     240m
     * 30   90cm      600m     275m
     * 40   120cm      720m     360m
     * 
     * 5
     * 2, 3
     * 
     * 
     * THREE,js1 = 1000mm
     */

    this.sizeList = [];
    this.sizeList[0] = {  size: 2, height: 60,  radius: 25  }
    this.sizeList[1] = {  size: 3, height: 120, radius: 30  }
    this.sizeList[2] = {  size: 4, height: 150, radius: 65  }
    this.sizeList[3] = {  size: 5, height: 200, radius: 85  }
    this.sizeList[4] = {  size: 6, height: 220, radius: 110  }
    this.sizeList[5] = {  size: 7, height: 250, radius: 120  }
    this.sizeList[6] = {  size: 8, height: 280, radius: 140  }
    this.sizeList[7] = {  size: 10, height: 330, radius: 160  }
    this.sizeList[8] = {  size: 15, height: 400, radius: 200  }
    this.sizeList[9] = {  size: 20, height: 500, radius: 240  }
    this.sizeList[10] = {  size: 30, height: 600, radius: 275  }
    this.sizeList[11] = {  size: 40, height: 720, radius: 360  }

    this.objectList = []; //  
    this.openList = [];   //  

    this.past = Date.now();

    Tweener.init();

    //  
    const _gravity = 9.8;

    //  
    this.count = 0
    this.minStep = 100;

    this.hueMin = 0.0;
    this.hueMax = 1.0;
    this.saturation = 0.65;
    this.lightness = 0.6;

    this.areaSize = 150;


    this.isLoopEnable = true;
    //  
    document.addEventListener("visibilitychange", () => {
      if (document.hidden) {
        this.isLoopEnable = false;
      } else {
        this.isLoopEnable = true;
      }
    });


    let _pastTime = 0;
    let _time = 0;
    let __loop = ()=>
    {
      window.requestAnimationFrame( __loop.bind( this ) );

      _time += this.aw.time.delta;

      if( !this.isLoopEnable )
      {
        return;
      }

      if( ( _time - _pastTime ) * 1000 >= this.minStep )
      {
        _pastTime = _time;
        let _len = this.sizeList.length;
        var _bullet = this.sizeList[ ~~( Math.random() * Math.random() * Math.random() * _len) ];

        //  
        //  
        //  
        var _r = this.areaSize;
        var _rad = Date.now() * 0.001;
        var _x = Math.cos( _rad ) * _r;
        var _y = 0;
        var _z = Math.sin( _rad ) * _r;
        var _startPos = new THREE.Vector3( _x * 0.1, _y, _z * 0.1 );  //  


        _r = _r * ( 1.0 - Math.random() * Math.random() );
        _rad = Math.random() * Math.PI * 2
        _x = Math.cos( _rad ) * _r;
        _y = 100 + Math.random() * 20;
        _z = Math.sin( _rad ) * _r;
        var _endPos = new THREE.Vector3( _x, _y, _z );   //  

        //  
        var _t = Math.sqrt( ( _endPos.y - _startPos.y ) * 2 / _gravity );

        //  
        var _v0 = _gravity * _t;

        //    2 - 40
        var _size = 1;

        //  update.
        _endPos.y = _bullet.height * ( Math.random() * 0.1 + 1.0 );
        _size = _bullet.radius;
        _t = Math.sqrt( ( _endPos.y - _startPos.y ) * 2 / _gravity );
        _v0 = _gravity * _t

        //  
        var _geometry = new THREE.IcosahedronGeometry( 0.25, 1 );
        var _material = new THREE.MeshBasicMaterial({color: 0xFFFFFF,transparent:true})
        var _mesh = new THREE.Mesh( _geometry, _material );
        _mesh.position.copy( _startPos );
        this.scene.add( _mesh );

        var _partColors = [
                    new THREE.Color().setHSL( Math.random() * ( this.hueMax - this.hueMin ) + this.hueMin, this.saturation, this.lightness ),  //    
                    new THREE.Color().setHSL( Math.random() * ( this.hueMax - this.hueMin ) + this.hueMin, this.saturation, this.lightness ),  //  
                    new THREE.Color().setHSL( Math.random() * ( this.hueMax - this.hueMin ) + this.hueMin, this.saturation, this.lightness )   //  
                ];

        //  
        var _params = new fireWorkParams({
          startPosition: _startPos,
          endPosition: _endPos,
          fworkGravity: _gravity,
          fworkDuration: _t,
          fworkSpeed: _v0,
          partColors: _partColors,
          size: _size,
          textures: this.textures
        });

        //  
        this.shot( _params, _startPos, _endPos, _mesh );

        

      }



    }
    __loop();

    if( window.location.href.indexOf('#debug') != -1 )
    {
      this.pane = new Pane();
      this.pane.addBinding(this.aw.time, 'scale', {
        min: -1.0,
        max: 3.0,
        step: 0.01
      });
      // this.pane.addBinding(this.cameraFov, 'lends', {
      //   options: {
      //     '8mm': 8,
      //     '16mm': 16,
      //     '24mm': 24,
      //     '28mm': 28,
      //     '35mm': 35,
      //     '50mm': 50,
      //     '85mm': 85,
      //     '100mm': 100
      //   }
      // }).on('change', (ev) => {
      //   // console.log(ev.value.toFixed(2));
      //   // if (ev.last) {
      //   //   console.log('(last)');
      //   // }

      //   this.aw.camera.focalLengthToFOV( ev.value );

      // });
      this.pane.addBinding(this, 'areaSize', {
        min: 50,
        max: 500,
        step: 10
      });

      this.pane.addBinding(this, 'minStep', {
        min: 16,
        max: 1000,
        step: 1
      });

      this.pane.addBinding(this.aw.sky.material, 'color',{
        color: {type: 'float'}
      });

      this.pane.addBinding(this, 'hueMax', {
        min: 0.0, max: 1.0,step: 0.01
      });

      this.pane.addBinding(this, 'hueMin', {
        min: 0.0, max: 1.0,step: 0.01
      });

      this.pane.addBinding(this, 'saturation', {
        min: 0.0, max: 1.0,step: 0.01
      });

      this.pane.addBinding(this, 'lightness', {
        min: 0.0, max: 1.0,step: 0.01
      });

      // console.log( this.pane, this.aw.time.scale )
      // this.pane.addInput( this.aw.time, 'scale', {
      //   min: 0.0,
      //   max: 2.0,
      //   step: 0.01
      // });
    }

  }

  update()
  {
    window.requestAnimationFrame( this.update.bind(this) );

    let _delta = this.aw.time.delta;

    //  
    let _len0 = this.objectList.length;
    while( _len0 )
    {
      _len0--;
      let __fw = this.objectList[ _len0 ];
      __fw.time += _delta;
      __fw.time = __fw.time < 0.0 ? 0.0 : __fw.time;

      let _mesh = __fw.mesh;
      let _params = __fw.params.value;

      //  
      var _posy = _params[0].fworkSpeed * __fw.time - 1/2 * _params[0].fworkGravity * __fw.time * __fw.time;

      //  
      var _posx = __fw.startPosition.x + ( __fw.endPosition.x - __fw.startPosition.x ) * __fw.time / _params[0].fworkDuration;
      var _posz = __fw.startPosition.z + ( __fw.endPosition.z - __fw.startPosition.z ) * __fw.time / _params[0].fworkDuration;

      _mesh.position.set( _posx, _posy, _posz );
      _mesh.material.opacity = Math.random()

      //  
      if( __fw.time > _params[0].fworkDuration )
      {
        this.objectList.splice( _len0, 1 );
        let __fg = new fireWorkGenerator( __fw.params, this.scene, this.textures );

        //  next step
        this.openList.push( __fg );

        //  kill object
        this.scene.remove( _mesh );
        _mesh.geometry.dispose()
        if( _mesh.material.map ) _mesh.material.map.dispose();
        _mesh.material.dispose();
        _mesh = null;

        __fw.mesh = null;
        __fw.params = null;
        __fw = null;

        this.count ++;
        document.querySelector('.shots').textContent = this.count;

      }

    }

    //  
    let _len1 = this.openList.length;
    while( _len1 )
    {
      _len1--;
      let __fg = this.openList[ _len1 ];
      __fg.update();

      //  kill check
      if( ! __fg.isAlive )
      {
        this.openList.splice( _len1, 1 );
        __fg.dispose();
        __fg = null;
      }

    }

  }

  //  
  shot( _params, _startPos, _endPos, _mesh )
  {

    var _fireworksSphere = {
      startPosition: _startPos,
      endPosition: _endPos,
      params: _params,
      time: 0.0,
      mesh: _mesh
    };

     this.objectList.push( _fireworksSphere );

  }

  //  
  fire( _params, _endPos, _override = {} )
  {
    let __fg = new fireWorkGenerator( _params, this.scene, this.textures );
    this.openList.push( __fg );
  }

}
