import * as THREE from 'three';

export default class Time
{
  constructor( props = {} )
  {
    this.state = {};
    this.props = props;

    this.init();
    this.update();
  }

  init()
  {
    this.clock = new THREE.Clock()
    this.time = 0;
    this.delta = 0;
    this.scale = 1.0;

    this.event = new CustomEvent('tick');
    this.elem = document;
  }

  update()
  {
    this.timeIntervalKey = window.requestAnimationFrame( () => {  this.update();  } );

    this.delta = this.clock.getDelta() * this.scale;
    this.time += this.delta;

    this.elem.dispatchEvent( this.event );
  }

  start()
  {
    this.stop()
    this.clock.start();
    this.update();
  }

  stop()
  {
    this.clock.stop();
    if( this.timeIntervalKey ){ window.cancelAnimationFrame( this.timeIntervalKey );  }
  }
}